// src/components/ResultScreen.js
import React, { useEffect, useState } from 'react';
import { submitScore, subscribeTopScores } from '../firebase';
import KakaoIcon  from '../assets/kakaotalk.png';
import LinkIcon   from '../assets/link.png';
import GooglePlay from '../assets/google.svg';
import AppStore   from '../assets/appstore.svg';
import CI         from '../assets/CI-black.png';

export default function ResultScreen({ score, onRetry }) {
  const [message, setMessage] = useState('');
  const [leaderboard, setLeaderboard] = useState([]);

  useEffect(() => {
    //  
    if (score < 10) setMessage(' ,  !');
    else if (score >= 60) setMessage('  MVP');
    else if (score >= 50) setMessage('  !');
    else if (score >= 40) setMessage('.....?');
    else if (score >= 30) setMessage('  !');
    else if (score >= 20) setMessage('  !');

    //  
    submitScore(score).catch(console.error);
    //  5  
    const unsubscribe = subscribeTopScores(5, setLeaderboard);
    return () => unsubscribe();
  }, [score]);

  //   
  const handleKakaoShare = () => {
    if (!window.Kakao) return;
    window.Kakao.Link.sendDefault({
      objectType: 'feed',
      content: {
        title: '   ',
        description: ` ${score} !`,
        imageUrl: `${window.location.origin}/assets/preview.png`,
        link: {
          mobileWebUrl: window.location.href,
          webUrl:        window.location.href,
        },
      },
      buttons: [
        {
          title: ' ',
          link: {
            mobileWebUrl: window.location.href,
            webUrl:        window.location.href,
          },
        },
      ],
    });
  };

  //   
  const handleCopyLink = () => {
    navigator.clipboard.writeText(window.location.href)
      .then(() => alert(' .'));
  };

  return (
    <div className="h-screen overflow-y-auto">
      {/*    (2/3  ) */}
      <div className="flex flex-col items-center justify-center px-4 h-[66vh]">
        <p className="text-white text-lg mb-5"> </p>
        <p className="text-white text-5xl font-bold">{score}</p>
        <p className="text-white text-xl mt-8 mb-12">{message}</p>
        <button
          onClick={onRetry}
          className="px-8 py-2 bg-[#B3FF00] text-black font-bold rounded-full shadow hover:scale-105 transition"
        >
           
        </button>

        {/*   5 */}
        <div className="mt-8 w-full max-w-xs bg-white bg-opacity-20 backdrop-blur-sm rounded-lg p-4">
          <h3 className="text-white text-base mb-2 font-semibold">  </h3>
          {leaderboard.length > 0 ? (
            <ol className="list-decimal list-inside text-white space-y-1">
              {leaderboard.map(({ key, score }, idx) => (
                <li key={key} className={idx === 0 ? 'font-bold' : ''}>
                  {score}
                </li>
              ))}
            </ol>
          ) : (
            <p className="text-white"> </p>
          )}
        </div>
      </div>

      {/*   (  ) */}
      <div className="bg-gray-100 w-full px-4 py-10 space-y-8">
        {/*  */}
        <div className="flex flex-col items-center">
          <p className="text-gray-700 mb-2"></p>
          <div className="flex space-x-6">
            <button onClick={handleKakaoShare} aria-label=" ">
              <img src={KakaoIcon} alt=" " className="w-8 h-8" />
            </button>
            <button onClick={handleCopyLink} aria-label=" ">
              <img src={LinkIcon} alt=" " className="w-8 h-8" />
            </button>
          </div>
        </div>

        {/*   */}
        <div className="flex flex-col items-center space-y-1">
          <p className="text-gray-600 text-sm">NO.1 / </p>
          <p className="text-gray-800 text-lg font-bold"> </p>
          <div className="flex space-x-4 mt-2">
            <a
              href="https://play.google.com/store"
              target="_blank"
              rel="noopener noreferrer"
              aria-label="Google Play "
            >
              <img src={GooglePlay} alt="Google Play " className="h-10" />
            </a>
            <a
              href="https://www.apple.com/app-store/"
              target="_blank"
              rel="noopener noreferrer"
              aria-label="App Store "
            >
              <img src={AppStore} alt="App Store " className="h-10" />
            </a>
          </div>
        </div>

        {/*  CI  */}
        <div className="flex justify-center">
          <img src={CI} alt="MatchUp CI" className="w-16" />
        </div>
      </div>
    </div>
  );
}
