// src/firebase.js

import { initializeApp } from "firebase/app";
import {
  getDatabase,
  ref,
  push,
  query,
  orderByChild,
  limitToLast,
  onValue
} from "firebase/database";

// Firebase  (   )
const firebaseConfig = {
  apiKey: process.env.REACT_APP_GOOGLE_API_KEY,
  authDomain: "hitgame-1e87c.firebaseapp.com",
  databaseURL: "https://hitgame-1e87c-default-rtdb.firebaseio.com",
  projectId: "hitgame-1e87c",
  storageBucket: "hitgame-1e87c.appspot.com",
  messagingSenderId: "939254271917",
  appId: "1:939254271917:web:80f5767b8cca88588d1b24",
  measurementId: "G-XPET2S0V5M"
};

// Firebase  
const app = initializeApp(firebaseConfig);

// Realtime Database 
export const db = getDatabase(app);

/**
 *   /leaderboard  .
 * @param {number} score
 * @returns {Promise}
 */
export function submitScore(score) {
  const listRef = ref(db, 'leaderboard');
  return push(listRef, {
    score,
    timestamp: Date.now()
  });
}

/**
 *  N   .
 * @param {number} limit   
 * @param {(entries: {key:string,score:number}[]) => void} callback
 * @returns {() => void}   
 */
export function subscribeTopScores(limit, callback) {
  const q = query(
    ref(db, 'leaderboard'),
    orderByChild('score'),
    limitToLast(limit)
  );
  const unsubscribe = onValue(q, snapshot => {
    const data = [];
    snapshot.forEach(child => {
      data.push({ key: child.key, score: child.val().score });
    });
    //  
    callback(data.sort((a, b) => b.score - a.score));
  });
  return unsubscribe;
}
